import React from "react";
import { Form } from "react-bootstrap"
import { connect } from "react-redux"
import {
    UPDATE_DASHBOARD_CUSTOMER_MONTH_ACTION,
    UPDATE_DASHBOARD_CUSTOMER_YEAR_ACTION,
    UPDATE_DASHBOARD_CUSTOMER_PRODUCT_ACTION,
    UPDATE_CUSTOMER_YEAR_ACTION,
} from "../../../redux/actions/actions"


class CustomerSelect extends React.Component {
    state = {
        year: this.props.customer_year,
    }

    static getDerivedStateFromProps = (props, state) => {
        state.year = parseInt(props.customer_year)
        return state
    }

    handleYearChange = async (year) => {
        await this.props.UPDATE_CUSTOMER_YEAR_ACTION(year)
    }


    render() {
        const years = [2022, 2021]

        return (
            <div className="pie-select">

                <Form.Group className="mb-4 pie-select__child" controlId="exampleForm.ControlInput1">
                    <Form.Label>Year</Form.Label>
                    <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleYearChange(e.target.value)}>
                        <option>{this.state.year}</option>
                        {years.map((year, idx) =>
                            parseInt(this.state.year) === year ? null : <option key={idx} value={year}>{year}</option>
                        )}

                    </Form.Select>
                </Form.Group>

            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        customer_year: state.update_reducer.customer_year
    };
};

const mapDispatchToProps = {
    UPDATE_CUSTOMER_YEAR_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(CustomerSelect);
